.texttitle {
  display: inline-block;
  width: 320px;
  height: 60px;
  padding-top: 10px;
  background-color: rgba(224, 14, 196, 0.493);
  font-size: 40px;
  font-weight: 900;
  color: rgb(255, 223, 78);
  margin-top: 50px;
  margin-bottom: 70px;
  border-radius: 8px;
}

.sumcircl {
  font-size: 35px;
  font-weight: 700;
  text-shadow: rgb(179, 190, 255) 0px 0px 20px;

}

.circlwraper {
  max-width: 600px;
  height: 100%;
  border: 1px solid #ccc;
  box-shadow: 0 0 20px 10px rgba(175, 175, 175, 0.692);
  border-radius: 8px;
  margin: 50px auto;
  text-align: center;
  background: url("../img/71.jpg");

}


.imgcircl {
  top: 35px;
  margin: 0 auto;

}

@keyframes circlanime {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(3185deg);
  }
}

@keyframes circlanime1 {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(3300deg);
  }
}

@keyframes circlanime2 {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(3360deg);
  }
}

@keyframes circlanime3 {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(3420deg);
  }
}

@keyframes circlanime4 {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(3485deg);
  }
}

@keyframes circlanime5 {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(3960deg);
  }
}

.imgcursor {
  top: 10px;

}

.imgkurs {
  margin-left: 168px;
  margin-top: -36px;
  position: absolute;
  z-index: 10;
}


.btncircl {
  color: #fff;
  background-color: rgb(211, 14, 40);
  width: 120px;
  font-size: 20px;
  height: 60px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  margin-bottom: 70px;
}

.btncircl:hover {
  box-shadow: 0 0 30px 0 rgb(248, 58, 255);
  color: rgb(255, 230, 1);
}

.txtcircl {
  font-size: 25px;
  margin-bottom: 40px;
  margin-top: 50px;
  color: red;
}

input:focus,
select:focus,
textarea:focus,
button:focus {
  outline: none;
}



@media (max-width: 480px) {

  .imgkrug {
    width: 290px;
  }

  .imgkurs {
    width: 60px;
    margin-left: 122px;
  }
}

@media (max-width: 370px) {
  .texttitle {
    width: 280px;
    font-size: 30px;
    padding-top: 16px;
  }
}