* {
  padding: 0;
  margin: 0;
  border: 0;
}



:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  font-family: 'Roboto', sans-serif;

  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  text-size-adjust: 100%;
  box-sizing: border-box;

}

body {
  overflow-x: hidden;
}


*,
*:before,
*:after {
  box-sizing: inherit;


}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

.container {
  max-width: 1130px;
  padding: 0px 15px;

  width: 100%;
  margin: 0 auto;

}

.clearfix:after {
  content: '';
  display: table;
  width: 100%;
  clear: both;
}

body {

  background: url("../img/115.png");
}